.hv4-root {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 24px 14px;
  background:
    radial-gradient(circle at 14% 10%, #d9f8e8 0%, #ecfff6 34%, transparent 52%),
    radial-gradient(circle at 88% 18%, #ffe7bd 0%, #fff2dd 34%, transparent 52%),
    #f4f1e8;
}

.hv4-phone {
  position: relative;
  width: 300px;
  height: 620px;
  border-radius: 46px;
  background: #f2f2f3;
  border: 5px solid #cfd0d4;
  overflow: hidden;
  box-shadow: 0 24px 48px rgba(0, 0, 0, 0.18);
}

.hv4-phone::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 26px;
  background: #000;
  border-radius: 999px;
  z-index: 100;
}

.hv4-page {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #fff;
}

.hv4-status {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 20px 8px;
  font-size: 12px;
  font-weight: 700;
  background: #f2f2f3;
}

.hv4-battery {
  position: relative;
  width: 18px;
  height: 10px;
  border: 1.5px solid #111;
  border-radius: 2px;
  display: inline-flex;
  align-items: center;
  padding: 1px;
  box-sizing: border-box;
}

.hv4-battery__level {
  width: 100%;
  height: 100%;
  background: #111;
  border-radius: 1px;
}

.hv4-battery__cap {
  position: absolute;
  right: -3px;
  top: 2px;
  width: 2px;
  height: 4px;
  background: #111;
  border-radius: 1px;
}

.hv4-content {
  flex: 1;
  overflow-y: auto;
  padding: 12px;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.hv4-content::-webkit-scrollbar {
  display: none;
}

.hv4-shell {
  border-radius: 28px;
  border: 1px solid #d6e1d8;
  background:
    linear-gradient(180deg, rgba(226, 255, 244, 0.45) 0%, rgba(255, 255, 255, 0) 28%),
    #ffffff;
  box-shadow: 0 18px 40px rgba(16, 185, 129, 0.1);
  padding: 14px;
}

.hv4-banner {
  border-radius: 22px;
  border: 1px solid #9ae6be;
  background: linear-gradient(135deg, #ebffef 0%, #f8ffef 100%);
}

.hv4-mode-grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 6px;
}

.hv4-mode-chip {
  border-radius: 12px;
  border: 1px solid #d4d4d8;
  background: #fafafa;
  font-size: 10px;
  font-weight: 800;
  padding: 6px 8px;
  color: #27272a;
  text-align: center;
}

.hv4-nav {
  flex-shrink: 0;
  margin: 0 8px 8px;
  border: 1px solid #e5e7eb;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(12px);
  border-radius: 18px;
  padding: 8px 6px 10px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 4px;
}

.hv4-nav__item {
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2px;
  color: #3f3f46;
  border-radius: 12px;
  padding: 4px 2px;
}

.hv4-nav__item--active {
  background: #111827;
  color: #fff;
}

.hv4-nav__icon {
  font-size: 14px;
  line-height: 1;
}

.hv4-nav__label {
  font-size: 10px;
  line-height: 1;
  font-weight: 700;
}
